/**
 * @file cell in bookcase
 * @author Yangholmes 2025-01-24
 */

import { defineComponent, ref, watch } from 'vue';

import useTRouter from '@/composite/useTRouter';
import { useNaiveFeedback } from '@/composite/useNaiveFeedback';
import { I18nT, useI18n } from 'vue-i18n';

import cn from 'classnames';

import BasicLayout from '../layout/BasicLayout';
import BasicTopBar from '../layout/BasicTopBar';
import DoubleDeckerContainer from '@/components/doubleDeckerContainer/DoubleDeckerContainer';
import SvgIcon from '@/components/svgIcon/SvgIcon';
import BookList from './BookList';
import Empty from '@/components/empty/Empty';
import BarcodeDecker from '@/components/barcode/BarcodeDecker';

import { NButton, NDropdown, NInput } from 'naive-ui';

import db from '@/services/db';
import { Bookcase, Cell } from '../bookcase/utils';
import { BookInCellRawCover } from '../book/utils';
import { escapeRegExp } from './utils';

import styles from './cell.module.less';

export default defineComponent({
  name: 'Cell',
  setup() {
    const { t } = useI18n({ useScope: 'global' });
    const { route, router } = useTRouter();
    const { dialog } = useNaiveFeedback();

    const bookcaseInfo = ref<Bookcase>();
    const cellInfo = ref<Cell>();
    const rawList = ref<BookInCellRawCover[]>([]);
    watch(() => route.params, (params) => {
      const cellId = params.cellId as string;
      db.bookcase.queryCellInfo(cellId).then(cell => {
        cellInfo.value = cell;
        db.bookcase.queryBookcaseById(cell?.bookcaseId!).then(bookcase => {
          bookcaseInfo.value = bookcase;
        });
      });
      db.book.queryBooksByCellId(cellId).then(res => {
        rawList.value = res;
        onSearch();
      });
    }, {
      immediate: true,
    });

    const onAddBook = (isbn?: string) => {
      if (!cellInfo.value) {
        return;
      }
      const { id, bookcaseId } = cellInfo.value;
      router.push({
        path: '/book/new',
        query: {
          isbn: isbn || '',
          bookcaseId: bookcaseId,
          cellId: id,
        }
      });
    };

    const onNewBarcode = (isbn: string) => {
      return new Promise<boolean>((resolve) => {
        dialog.success({
          title: t('pages.cell.newBarcodeDialog.title'),
          content: t('pages.cell.newBarcodeDialog.content', [isbn]),
          closable: false,
          positiveText: t('common.yes'),
          negativeText: t('common.no'),
          onPositiveClick: () => {
            onAddBook(isbn);
            resolve(true);
          },
          onNegativeClick: () => {
            resolve(false);
          }
        });
      });

    };

    const onAddByManual = () => {
      onAddBook();
    };

    const keyword = ref<string>('');
    const bookList = ref<BookInCellRawCover[]>([]);
    const onSearch = () => {
      if (keyword.value === '') {
        bookList.value = rawList.value;
      }
      const keys = escapeRegExp(keyword.value).split(' ').map(k => k.trim()).filter(key => !!key);
      const regexp = new RegExp(`${keys.join('|')}`);
      const fBookList = rawList.value.filter(book => {
        let result = false;
        const { title, authors, isbn } = book;
        // 找书名
        result ||= regexp.test(title);
        // 找作者
        result ||= authors.some(author => regexp.test(author));
        // 找 isbn
        result ||= regexp.test(isbn);
        return result;
      });
      bookList.value = fBookList;
    };

    const renderBooklist = () => {
      // 书架上没有书
      if (rawList.value.length === 0) {
        return <Empty
          title={t('pages.cell.empty.title')}
          description={
            <I18nT scope="global" keypath="pages.cell.empty.description">
              <SvgIcon name="plus" />
            </I18nT>
          } />;
      }
      // 书架上有书，但过滤之后没有结果
      if (bookList.value.length === 0) {
        return <Empty title={t('pages.cell.noResult.title')} description={t('pages.cell.noResult.description')} />;
      }
      // 正常渲染
      return <div style={{ flex: 1, overflow: 'hidden' }}><BookList
        data={bookList.value}
        onPreviewBook={(book) => {
          router.push({
            path: `/book/preview/id/${book.id}`
          });
        }}
        onEditBook={(book) => {
          router.push({
            path: `/book/edit/id/${book.id}`
          });
        }}
        onDeleteBook={(book) => {
          db.book.deleteBook(book.id).then(() => {
            bookList.value = bookList.value.filter(b => b.id !== book.id);
          });
        }}
      /></div>;
    };

    return () => <DoubleDeckerContainer>
      {{
        default: (go: () => void) => <BasicLayout topBar={
          <BasicTopBar rightSlots={[
            <NDropdown trigger="click" placement="bottom-end"
              options={[{
                key: 'barcodeScan',
                label: t('pages.cell.topBar.rightSlots.barcodeScan'),
                icon: () => <SvgIcon name="barcodeScan" />
              }, {
                key: 'manual',
                label: t('pages.cell.topBar.rightSlots.manual'),
                icon: () => <SvgIcon name="memo" />
              }]}
              onSelect={(key) => {
                if (key === 'barcodeScan') {
                  go();
                } else if (key === 'manual') {
                  onAddByManual();
                }
              }}
            >
              <NButton>
                <SvgIcon name="plus" />
              </NButton>
            </NDropdown>
          ]}>
            {bookcaseInfo.value?.name}
          </BasicTopBar>
        }>
          <div class={cn(styles.cell)}>
            <NInput
              placeholder={t('pages.cell.search.placeholder')}
              disabled={rawList.value.length === 0}
              value={keyword.value}
              onUpdateValue={(v) => {
                keyword.value = v;
              }}
              onKeyup={(e) => {
                if (e.key === 'Enter') {
                  keyword.value = keyword.value.trim();
                  onSearch();
                }
              }}
            />
            {/* 陈列所有书籍 */}
            {renderBooklist()}
          </div>
        </BasicLayout>,
        decker: () => <BarcodeDecker onBarcodeDetect={onNewBarcode} />
      }}
    </DoubleDeckerContainer>;
  }
});
